body{
    background-color: wheat;  
}
.todo-item input{
    border-color: black; 
    padding: 5px;
    border-radius: 5px;
   
    
}
.todo-item input::placeholder{
    color: black;
    position: relative;
}
.todo-item button{
    position: absolute;
    color: blue;
    cursor: pointer;
    background-color: white; 
    padding:5px ;
    border-radius: 5px;
    

}
.todo-list {
    text-align: center;
    padding: 100px;
    margin:auto;
}