.list{
    font-size: medium;
    width: 200px;   
    align-items: center;
    margin:auto;
}
.list p{
    
    position:relative;
    background-color: rgb(255, 123, 0);
    align-items: center;
    width: 90%;
    border-radius: 5px;
  
}

.list p span{
    position: absolute;
    cursor: pointer;
    color:black;
    left:90%;
    top:5px;
}

.list p input{
    background-color: transparent;
    border:0;
    color: black;
}
.style{
    right:20%;
    position: absolute;
    height: 20px;

}
.list p input:focus{
    outline:none;
}

